$red: #e20001;
$black: #191919;

.container {
  width: 100%;
  margin: 0 auto;
  .wrapper {
    position: relative;
    height: 100%;
    margin: 12% auto;
    width: 66%;
    .home {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 100%;
      p {
        font-size: 0.75rem;
        letter-spacing: 0.3rem;
        text-transform: uppercase;
        font-weight: 500;
      }
      h5 {
        margin-top: 260px;
        font-size: 24px;
        font-weight: 600;
        padding-right: 360px;
      }
    }
  }
}

